  -moz-animation: fadeInUp 0.2s ease forwards;
  -o-animation: fadeInUp 0.2s ease forwards;
  animation: fadeInUp 0.2s ease forwards;
}

.search-result-item:nth-child(1) { 
  -webkit-animation-delay: 0.02s;
  -moz-animation-delay: 0.02s;
  -o-animation-delay: 0.02s;
  animation-delay: 0.02s; 
}
.search-result-item:nth-child(2) { 
  -webkit-animation-delay: 0.04s;
  -moz-animation-delay: 0.04s;
  -o-animation-delay: 0.04s;
  animation-delay: 0.04s; 
}
.search-result-item:nth-child(3) { 
  -webkit-animation-delay: 0.06s;
  -moz-animation-delay: 0.06s;
  -o-animation-delay: 0.06s;
  animation-delay: 0.06s; 
}
.search-result-item:nth-child(4) { 
  -webkit-animation-delay: 0.08s;
  -moz-animation-delay: 0.08s;
  -o-animation-delay: 0.08s;
  animation-delay: 0.08s; 
}

/* Efeito de loading para imagens - Otimizado */
.search-result-item img[src=""],
.search-result-item img:not([src]) {
  /* Fallback para gradiente */
  background: #2a2a2a;
  background: -webkit-gradient(linear, left top, right top, 
    color-stop(25%, #2a2a2a), 
    color-stop(50%, #3a3a3a), 
    color-stop(75%, #2a2a2a));
  background: -webkit-linear-gradient(90deg, #2a2a2a 25%, #3a3a3a 50%, #2a2a2a 75%);
  background: -o-linear-gradient(90deg, #2a2a2a 25%, #3a3a3a 50%, #2a2a2a 75%);
  background: linear-gradient(90deg, #2a2a2a 25%, #3a3a3a 50%, #2a2a2a 75%);
  
  background-size: 200% 100%;
  
  /* Animao com prefixos */
  -webkit-animation: loading 1.5s infinite;
  -moz-animation: loading 1.5s infinite;
  -o-animation: loading 1.5s infinite;
  animation: loading 1.5s infinite;
}

/* Keyframes com prefixos */
@-webkit-keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

@-moz-keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

@-o-keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* GPU acceleration para elementos crticos */
.search-container,
.search-layout,
.search-input-area,
.virtual-keyboard,
.search-results,
.results-section,
.results-grid,
.search-result-item,
.loading,
.search-placeholder,
.no-results {
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
} 